.icon{
    height: 60px;
    width: 60px;
    object-fit: cover;
}
.bg-info{
    background-color:#cd88f76e;
}
.login-button{
    background:transparent;
    border-radius:5px;
    margin-left: 5px;
    padding: 5px;
    border:none !important;
    color:white;
}
.login-button:hover{
    background-color:rgb(65, 203, 253);
    border-radius: 10px;
}

#brand{
    margin-top: 7px;
    font-size: 20px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.navbtns{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
    margin-left:40px;
}
.nav-subbtns{
    text-decoration: none;
}
.navbtns:hover{
    border-radius: 10px;
    cursor: pointer;

}